import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

const DevRadar = () => {
  const { projectBanner } = useStaticQuery(
    graphql`
      query {
        projectBanner: file(relativePath: { eq: "devradar.png" }) {
          childImageSharp {
            fluid(maxWidth: 510, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  )

  return <Img fluid={projectBanner.childImageSharp.fluid} />
}

export default DevRadar
